@import url(https://fonts.googleapis.com/css2?family=Courgette&display=swap);
body {
  margin: 0;
  font-family: 'Courgette', cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

button {
  font-family: 'Courgette', cursive;
  background: none;
  border: none;
}

.App {
  text-align: center;
  font-family: 'Courgette', cursive;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

:root {
  --RUBY: 233,67,54;
  --SAPPHIRE: 0,104,175;
  --EMERALD: 14,144,70;
  --DIAMOND: 255,255,255;
  --GOLD: 255,239,1;
  --ONYX: 0,0,0;
  --PLAYER-BOARD-CARD-OPACITY: 0.5;
  --TOKEN-BACKGROUND: linear-gradient(rgb(243, 208, 160), rgb(204, 164, 133));
}

.ONYX {
  background: rgb(0,0,0);
  background: rgb(var(--ONYX));
  color: white;
}
.RUBY {
  background: rgb(233,67,54);
  background: rgb(var(--RUBY));
  color: white;
}
/* Green */
.SAPPHIRE {
  background: rgb(0,104,175);
  background: rgb(var(--SAPPHIRE));
  color: white;
}
.EMERALD {
  background: rgb(14,144,70);
  background: rgb(var(--EMERALD));
  color: white;
}
.DIAMOND {
  background: rgb(255,255,255);
  background: rgb(var(--DIAMOND));
  color: black;
}
.GOLD {
  background: rgb(255,239,1);
  background: rgb(var(--GOLD));
  color: white;
}

